@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  background-color: #101010;
  font-family: 'Inter', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  max-width: min(1168px, 100vw - 150px);
  margin: auto;
  color: white;
}

nav {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding-top: 3rem;
    font-weight: 600;
    color: gray;
}

.logo {
    font-size: 1rem;
    font-weight: 600;
}

.logo {
    width: 1rem;
    height: 1rem;
    border-radius: 0.2rem;
    background-color: white;
}

#active {
    color: white
}

.nav-links {
    display: flex;

}

.nav-links>li {
    list-style-type: none;
    padding-left: 4rem;
}

.hero {
    padding-top: 15rem;
}

h1 {
    font-size: 2.5rem;
    font-weight: 500;
    margin: 0;
}

.desc {
    font-weight: 300;
    margin-bottom: 0;
}

hr {
    color: white;
    border: 2px solid white;
    margin-left: 0;
    animation-name: line;
    animation-duration: 2s;
    animation-timing-function: cubic-bezier(0.23, .8, 0.320, 1);
}

@keyframes line {
    from {
        width: 0%;
    }
    to {
        width: 100%;
    }
}


.projectarrow {
    margin-top: 21rem;
    font-weight: 500;
}
